import React from 'react'
import Card from '../components/Card'
import Intro from '../components/Intro'

const intro = {
	content: `segala upaya yang dilakukan untuk melakukan pemasaran suatu produk atau jasa melalui atau menggunakan media elektronik atau Internet. Huruf 'e' dalam e-marketing ini berarti elektronik yang artinya kegiatan pemasaran yang dimaksud dilaksanakan secara elektronik melalui jaringan Internet. Dengan terciptanya teknologi Internet, banyak istilah baru yang menggunakan awalan huruf e, seperti halnya: e-mail, e-business, e-gov, e-society, dll.`,
}

const features = [
	
	{title: 'Materi Belajar', desc: 'Materi belajar disusun secara sistemaris dengan metode dan teknologi terbaru. Materi Belajar menggunakan berbagai media'},

	{title: 'Waktu Belajar', desc: 'Waktu belajar disusun secara sistemaris dengan metode dan teknologi terbaru. Materi Belajar menggunakan berbagai media'},

	{title: 'Metode Belajar', desc: 'Metode belajar disusun secara sistemaris dengan metode dan teknologi terbaru. Materi Belajar menggunakan berbagai media'}

]

const cards = [
	{title: 'Menjadi Web Programmer', imageUrl: 'https://wallpaperaccess.com/full/314799.jpg'},
	{title: 'Belajar Pemgrogaram PHP', imageUrl: 'https://cdn.hipwallpaper.com/i/26/2/cqPORx.jpg'},
]

const Premium = () => (
	<div className="container">
		<h1 style={{textAlign: 'center'}}>PREMIUM ONLINE COURSE</h1>
			<div className="row" style={{marginTop: 40, marginBottom:100}}>
				<Intro content={intro.content}/>

				<div className="col-md-6" style={styles.box450border}>
					{
						features.map(feature => (
								<div>
									<h5><strong>{feature.title}</strong></h5>
									<p>{feature.desc}</p>
								</div>
							))
					}
				</div>
			</div>

			<div className="row" style={{ marginBottom: 50}}>
				{
					cards.map(card =>
						<Card imageUrl={card.imageUrl} title={card.title}/>
					)
				}
			</div>
	</div>
)

const styles = {
	box450border: {
		height: '450px',
		border: '2px solid #f1f1f1',
		borderRadius: 10,
		padding: 30
	},
}

export default Premium