import React,{Component} from 'react'
import axios from 'axios'
import AudioPlayer from 'react-h5-audio-player'
import 'react-h5-audio-player/lib/styles.css'

class Blog extends Component {

	constructor(props) {
    super(props)
    this.state = {
      members: []
    }
  }

  componentDidMount() {
    axios.get('https://al-quran-8d642.firebaseio.com/data.json').then(response => {
      console.log('response', response)
      this.setState({members: response.data})
    })
    .catch(error => {
      console.log('error', error)
    })
  }


	render() {
    return (
	<div className="container">
		<h1 style={{textAlign: 'center'}}>Daftar Surah Al Qur'an</h1>
		<div className="row">
		{this.state.members.map((member) => (
        <div className="col-md-12" key={member.asma}>
            <div className="card" style={{margin: 10 }}>
                <div className="card-body">
                    <h5 className="card-title"><strong>{member.asma}</strong></h5>
                    <h5 className="card-title"><strong>Arti Surah : </strong>{member.arti}</h5>
                    <h5 className="card-title"><strong>Jumlah Ayat : </strong>{member.ayat}</h5>
                    <h5 className="card-title"><strong>Keterangan : </strong>{member.keterangan}</h5>
                    <h5 className="card-title"><strong>Play Audio : </strong><a>{member.audio}</a></h5>
					  <AudioPlayer
					    autoPlay
					    src="http://ia802609.us.archive.org/13/items/quraninindonesia/003AliImran.mp3"
					    onPlay={e => console.log("onPlay")}
					    // other props here
					  />
                </div>
            </div>
         </div>      
		))}
</div>
	</div>
	)
	}	
}

export default Blog