import React from 'react'
import Card from '../components/Card'
import Intro from '../components/Intro'

const intro = {
	content: `Semangat kami adalah menjadi bagian dari solusi. Dengan kurikulum yang berorientasi pada industri dan metode belajar yang telah teruji, kami ingin mendorong lahirnya banyak programmer baru yang siap menjadi solusi untuk berbagai permasalahan yang ada di Indonesia.`,
}

const cards = [
	{title: 'Weekend Bootcamp', imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-weekend.jpg', subtitle:'Kelas Belajar Coding yang diselenggarakan disetiap akhir pekan, berlokasi di Bandung dan Jakarta'},
	{title: 'Intensive Bootcamp', imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-intensive.jpg', subtitle:'Kelas Belajar Pemrograman Web dan Android secara intensif selama satu minggu dengan sistem karantina'},
]

const DevSchool = () => (

	<div className="container">
		<h1 style={{textAlign: 'center'}}>DEVELOPER SCHOOL</h1>
			<div className="row" style={{marginTop: 40, marginBottom:100}}>
				<Intro content={intro.content}/>

				<div className="col-md-6">
					<iframe src='https://www.youtube.com/embed/E7wJTI-1dvQ'
				        frameBorder='0'
				        allow='autoplay; encrypted-media'
				        allowFullScreen
				        title='video'
				        style={{width: '540px', height: '400px'}}
						/>
				</div>
			</div>

			<div className="row" style={{ marginBottom: 50}}>
				{
					cards.map(card =>
						<Card imageUrl={card.imageUrl} title={card.title} subtitle={card.subtitle}/>
					)
				}
			</div>
	</div>

)

export default DevSchool