import React,{Component} from 'react'
import axios from 'axios'

class Registration extends Component {

	constructor(props) {
    super(props)
    this.state = {
      members: [],
      first_name: '',
      last_name: '',
      email: '',
      buttonDisabled : false,
      formStatus : 'create',
      memberIdSelected: null,
      Keterangan: '',
    }
  }

  componentDidMount() {
    axios.get('https://reqres.in/api/users?page=1').then(response => {
    	console.log('response', response)
      this.setState({members: []})
    })
    .catch(error => {
      console.log('error', error)
    })
  }

  onSubmitHandler = (e) => {
  	console.log('form di submit')
    e.preventDefault()
    var payload = {
    	first_name: this.state.first_name,
    	last_name: this.state.last_name,
    	email: this.state.email
    }
    var url = 'https:reqres.in/api/users'
    this.addMember(url, payload)
  }

  inputOnChangeHandler = (event) => {
    this.setState({ [event.target.name]: event.target.value})
  }

  addMember = (url, payload) => {
    axios.post(url, payload).then(response => {
      console.log('response', response.data)
      var members = []
      members.push(response.data)
      this.setState({members, buttonDisabled: false, first_name:'', last_name:'', email:''})
      this.setState({Keterangan: 'Registered'})
    })
    .catch(error => {
      console.log(error)
    })
  }

	render() {
    return (
	<div className="container">
		<h1 style={{textAlign: 'center'}}>REGISTRATION</h1>
			<div className="row" style={{marginTop: 40, marginBottom:100}}>
				<div className="col-md-6" style={styles.box450}>
					<form onSubmit={this.onSubmitHandler}>
				        <div className="form-group">
				            <label>First Name</label>
				            <input type="text" className="form-control" name="first_name" value={this.state.first_name} onChange={this.inputOnChangeHandler}/>
				        </div>
				        <div className="form-group">
				            <label>Last Name</label>
				            <input type="text" className="form-control" name="last_name" value={this.state.last_name} onChange={this.inputOnChangeHandler}/>
				        </div>
				        <div className="form-group">
				            <label>Email</label>
				            <input type="email" className="form-control" name="email" value={this.state.email} onChange={this.inputOnChangeHandler}/>
				        </div>
				        <button type="submit" className="btn btn-success">Submit</button>
    				</form>
				</div>
				<div className="col-md-6" style={styles.box450border}>
				<label>Keterangan : <strong>{this.state.Keterangan}</strong></label>
				<div className="row">
					{this.state.members.map((member) => (
			        <div className="col-md-12" key={member.id}>
			            <div style={{margin: 10 }}>
			                    <label>First Name : {member.first_name}</label><br />
			                    <label>Last Name : {member.last_name}</label><br />
			                    <label>Email : {member.email}</label>	
			            </div>
			        </div>
				))}
				</div>
				
				
				</div>
			</div>
	</div>
	  )

  }
}

const styles = {
	box450border: {
		border: '2px solid #f1f1f1',
		borderRadius: 10,
		padding: 30
	},
	box450: {
		height: '450px',
		border: '2px solid #f1f1f1',
		borderRadius: 10,
	},
	greyText: {
		color: '#808080',
	}
}


export default Registration;